.modal-ing {
  position: sticky;
  top: 17%;

  max-width: 335px;
  /* max-height: 80%; */
  height: auto;
  width: 90%;
  border-radius: 20px;
  background: var(--color-4);
  padding: 40px 20px;
  overflow-y: auto;
  z-index: 10;
}
.cocktail-name::after {
  content: '';
  position: absolute;
  top: 40;
  left: 0;
  width: 100%;
  height: 1px;
  background: rgba(0, 0, 0, 0.1);
}
@media screen and (min-width: 768px) {
  .modal-ing {
    max-width: 600px;
    padding: 40px;
  }
}

