.modal-open {
  overflow: hidden;
}

.add-to-fav {
  border-radius: 12px;
  background: var(--violet, #7e8fdd);
  padding: 14px 28px;
  color: var(--text-white-color);
  content: '123';
}

.cocktail-name {
  color: rgba(36, 36, 36, 0.5);
  font-family: Poppins;
  font-size: 16px;
  font-weight: 600;
  line-height: 18px;
  text-transform: uppercase;
  margin-bottom: 40px;
  position: relative;
}

.ing-info-list {
  color: rgba(36, 36, 36, 0.9);
  font-family: Poppins;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  margin-bottom: 40px;
  list-style: disc;
  padding: 20px;
}

.ing-info-item:not(:last-child) {
  margin-bottom: 8px;
}

.ing-add-fav {
  border-radius: 12px;
  background: var(--violet, #7e8fdd);
  padding: 16px 44px;
  color: var(--text-white-color);
}

.ing-add-fav-text {
  color: #fdfdff;
  font-family: Poppins;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  /* 133.333% */
  letter-spacing: 0.36px;
  text-transform: uppercase;
}
.ing-name {
  font-family: Poppins;
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
  margin-bottom: 12px;
  color: #242424;
}
